import React from 'react'
import Login from './view/Login'
import Dashboard from './view/Dashboard'
import useLocalStorage from './util/userLocalStorage';
import {ContactsProvider} from './component/context/ContactsProvider'
import {ConversationsProvider} from './component/context/ConversationsProvider'
import { SocketProvider } from './component/context/SocketProvider';
function App() {
  const [id,setId] = useLocalStorage('id') 
  // const [password,setPassword] = useState('') 
  const dashboard = (
    <SocketProvider id={id}>
        <ContactsProvider>
          <ConversationsProvider id={id}>
            <Dashboard id={id}/>        
          </ConversationsProvider>
      </ContactsProvider>
    </SocketProvider>
  )

  return (
    <>
    {
      id ? 
      dashboard
      :
      <Login setId={setId}
      // setPassword={setPassword}
      />
    }
    </>
  );
}

export default App;
