import React,{useState} from 'react'
import {Tab, Nav, Button, Modal} from 'react-bootstrap'
import Conversations from './Conversations'
import Contacts from './Contacts'
import NewConversationModal from './NewConversationModal'
import NewContactModal from './NewContactModal'

const CONVERSATIONS_KEY = 'conversitions'
const CONTACTS_KEY = 'contacts'

function Sidebar(props) {
  const [activeKey, setActiveKey] = useState(CONVERSATIONS_KEY)
  const [modalToggle, setModalToggle] = useState(false)
  const conversationOpen = activeKey === CONVERSATIONS_KEY
  
  const modalClose = () => {
    setModalToggle(false)
  }

  return (
    <div style={{width:'250px'}} className="d-flex flex-column">
      <Tab.Container 
      activeKey={activeKey}
      onSelect={setActiveKey}>
        <Nav 
        variant="tabs"
        className='justify-content-center'>
          <Nav.Item>
            <Nav.Link
            eventKey={CONVERSATIONS_KEY}>
              Conversations
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link
            eventKey={CONTACTS_KEY}>
              Contacts
            </Nav.Link>
          </Nav.Item>
        </Nav>
        <Tab.Content 
        style={{borderRightStyle: 'ridge', height:'50vh'}} 
        className='border-right overflow-auto flex-grow-1'>
          <Tab.Pane 
          eventKey={CONVERSATIONS_KEY}>
            <Conversations />
          </Tab.Pane>
          <Tab.Pane 
          eventKey={CONTACTS_KEY}>
            <Contacts />
          </Tab.Pane>
        </Tab.Content>
        <div>
          Your id: 
          <span className="text-muted">{props.id}</span>
        </div>
        <Button onClick={()=>{setModalToggle(true)}}>
          New {conversationOpen ? 'Conversation' : 'Contact'}
        </Button>
      </Tab.Container>

      <Modal show={modalToggle}>
        {
          conversationOpen 
          ? 
          <NewConversationModal 
          modalClose={modalClose}/>
          : 
          <NewContactModal 
          modalClose={modalClose}/>
        }
      </Modal>
    </div>
  )
}

export default Sidebar